import React from "react";
import styled from "styled-components";

export function Button ({text, clickEvent, flow}) {

    const MyButton = styled.a`
      display: ${flow === "quiz" ? 'block' : 'inline-block'};
      ${flow === "quiz" && `margin-bottom: 30px;`}
      cursor: pointer;
      vertical-align: middle;
      text-decoration: none;
      transform-style: preserve-3d;
      line-height: 1.6;
      font-weight: bold;
      color: #382b22;
      padding: 1.25em 2em;
      background: wheat;
      border: 2px solid;
      border-radius: 0.75em;
      user-select: none;
      transition: transform .15s ease-out, background .15s ease-out;
      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #f9c4d2;
        border-radius: inherit;
        box-shadow: 0 0 0 2px #b18597, 0 0.625em 0 0 #ffe3e2;
        transform: translate3d(0, 0.75em, -1em);
        transition: transform .15s ease-out, background .15s ease-out;
      }
      &:hover {
        background: #ffe9e9;
        -webkit-transform: translateY(.25em);
        transform: translateY(.25em);
      }
    `;

    return <MyButton onClick={clickEvent}>{text}</MyButton>
}